import { Component, OnInit } from '@angular/core';
import { MainService } from '../../providers/mainService';
import { NgModule } from '@angular/core';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';

@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['./forgot-password.component.css']
})
export class ForgotPasswordComponent implements OnInit {
  
  forgotForm : any;
  constructor(private router: Router,private formBuilder:FormBuilder,public service:MainService) { }

  ngOnInit() {

    this.forgotForm=this.formBuilder.group({
      'email': ['', [Validators.required, Validators.pattern("[^ @]*@[^ @]*")]]
    });
  }
  forgot(val){
    console.log(`forgot password ${this.forgotForm.value}`)
    let forgotData={
      'email':val.email
    }
    this.service.postApi('user/forgotPassword',forgotData,0).subscribe(response=>{
      console.log(`response ${response}`)
    if(response['response_code']==200) {
      console.log('link send successfully...')
      this.service.showSuccess(response['response_message'])
      this.router.navigate(['/login'])

    }
    else{
      this.service.toastErr(response['response_message'])
      console.log(`in else ${response['response_message']}`)
    }
    },error=>{
      console.log('error occur',error)
      this.service.toastErr(error)
    })
  }

}
