import { Component, OnInit } from '@angular/core';
import { MainService } from '../../providers/mainService';

@Component({
  selector: 'app-content-management-view',
  templateUrl: './content-management-view.component.html',
  styleUrls: ['./content-management-view.component.css']
})
export class ContentManagementViewComponent implements OnInit {
controls
viewData:any;
  constructor(private service: MainService) { }

  ngOnInit() {
    this.controls = localStorage.getItem('control')
    this.service.getApi(`static/getStaticContent`, 1).subscribe(response => {
      console.log('res')
      if (response['response_code'] == 200) {
        console.log(' STATIC CONTENTS', response)
 if( this.controls == 'About Us'){
   this.viewData = response['result'][0].aboutUs
     console.log('About Us',this.viewData)
    }
    else if( this.controls == 'Terms And Conditions'){
      this.viewData = response['result'][0].termsAndConditions
      
      console.log('Terms And Conditions',this.viewData)
    }
    else if( this.controls == 'Privacy Policy'){
      this.viewData = response['result'][0].privacyPolicy
      console.log('Privacy Policy',this.viewData)
    }
        // this.viewData = response['result'][0].
             }
      else {
        this.service.toastErr(response['response_message'])
      }
    }, error => {
      console.log('error =>', error)
      this.service.toastErr('Something Went Wrong')
    })
  }

}
