import { Component, OnInit } from '@angular/core';
import { MainService } from '../../providers/mainService';
import { Router } from '../../../../node_modules/@angular/router';
import { FormGroup, FormBuilder, Validators } from '../../../../node_modules/@angular/forms';

@Component({
  selector: 'app-edit-user-detail',
  templateUrl: './edit-user-detail.component.html',
  styleUrls: ['./edit-user-detail.component.css']
})
export class EditUserDetailComponent implements OnInit {
  editForm:FormGroup
  customer: any;
   Id: any='';
  imageUrl: any;
  constructor(private service: MainService,private route: Router, private formBuilder: FormBuilder) { 
    this.editForm = formBuilder.group({
      name:  ['', Validators.compose([Validators.required, Validators.pattern(/^[a-zA-Z ]{2,40}$/), Validators.minLength(2)])],
      address: ['', Validators.compose([Validators.required])],
      phone: ['', Validators.compose([Validators.required, Validators.pattern(/^[1-9][0-9]{9}$/)])]

    })
  }

  ngOnInit() {
    this.Id = localStorage.getItem('_id of customer')
    console.log('id on nginit==>',this.Id)
    this.viewParticularCustomer()
  }
//==========================================================VIEW PARTICULAR CUSTOMER API ====================================================================
  viewParticularCustomer(){
   
    this.service.getApi(`admin/viewDetail/${this.Id}`,0).subscribe(response=>{
      console.log("**********************")
      if(response['response_code']==200){
          console.log(`view particular user ${response}`)
          
          this.customer = response[`result`]
          this.editForm.patchValue({
            name: this.customer.name || '',
            address: this.customer.address || '',
            phone: this.customer.mobile_no || '',           
            })

            if(response['result'].profilePic){ 
              // console.log('image exist') 
              this.imageUrl = this.customer.profilePic 
              } 
              else{
          //    console.log('image doesn"t exist') 
              this.imageUrl = 'assets/img/default_profile_image.png' 
              }  
          
            console.log(`in particular view==>${JSON.stringify(this.customer)}`)
           // this.service.showSuccess(response['response_message'])  
        }
        else{
          this.service.toastErr(response['response_message'])
        }
      },error=>
        {

      this.service.toastErr('Something Went Wrong')
    })
  }

  save(){
    console.log('save clicked !!')
    let postData = {
      "_id":this.Id,
      "userType":"CUSTOMER",
      "name":this.editForm.get('name').value,
      "mobile_no":this.editForm.get('phone').value,
      "address":this.editForm.get('address').value
    }
console.log('posting this data -->>', postData)
    this.service.postApi('admin/edit/',postData,0).subscribe(response=>{
      console.log("**********************")
      if(response['response_code']==200){
          console.log(`view particular user ${response}`)
          //this.service.showSuccess(response['response_message'])  
          this.route.navigate(['/dashboard'])
      }
      else{
        this.service.toastErr(response['response_message'])
      }
    },error=>{
      this.service.toastErr('Something Went Wrong')
    })
  }
  cancel(){
    console.log('cancel clicked !!')
    this.route.navigate(['/dashboard'])
  }
 
}
