import { Component, OnInit } from '@angular/core';
import { MainService } from '../../providers/mainService';
import { NgModule } from '@angular/core';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';
declare var $: any


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  user: any;
  loginForm : FormGroup;
  remberMe:any=[]
  // remberMeEmail:any
  // remberMePass:any
  // remberMeCheck:any
  token: any;
  constructor(private router: Router,private formBuilder:FormBuilder,public service:MainService) { }
  ngOnInit() {
      this.loginForm=this.formBuilder.group({
        'email':  ['', Validators.compose([Validators.required, Validators.pattern(/^[A-Z0-9_]+([\.][A-Z0-9_]+)*@[A-Z0-9-]+(\.[a-zA-Z]{2,3})+$/i), Validators.maxLength(50)])],
        //'email': ['', [Validators.required, Validators.pattern("[^ @]*@[^ @]*")]],
        //'pass':['', [Validators.required, Validators.minLength(8)]],
        'pass':['', Validators.compose([Validators.required,  Validators.maxLength(16), Validators.minLength(8)])],
        'remberMe': [false]
      });
      $(function() {
        $('#email,#pass').on('keypress', function(e) {
            if (e.which == 32)
                return true;
        });
      });
    if(localStorage.getItem('LoginData') != null) {
      let loginData = JSON.parse(localStorage.getItem('LoginData'))
      this.loginForm.controls['email'].setValue(loginData.email)
       this.loginForm.controls['pass'].setValue(loginData.pass)
      this.loginForm.controls['remberMe'].setValue(loginData.remberMe)
    }
  }
  login(val){
     // if(this.loginForm.dirty && this.loginForm.valid)
     // alert(`Email: ${this.loginForm.value.email} Password: ${this.loginForm.value.pass} `)
        console.log(this.loginForm.value)
        console.log(val)
        let loginData = {
                        "email": val.email,
                       "password": val.pass,
                        "userType":"SUPERADMIN"
                        }
                        console.log(loginData)
        this.service.postApi('user/login',loginData,0).subscribe( response =>{
        console.log(`Login Data====>${JSON.stringify(val)}`);
        if(response['response_code']==200){
          localStorage.token = response[`token`];
          localStorage._id = response[`result`][`_id`];
            console.log('successfully login',response['response_message'])

if (this.loginForm.value.remberMe == true) {
  console.log('remeber me')
  localStorage.setItem('LoginData', JSON.stringify(this.loginForm.value))
  } else {
  if (localStorage.getItem('LoginData') != null) {
  localStorage.removeItem('LoginData')
  }
  }
            this.service.showSuccess(response['response_message'])
            this.router.navigate(['/dashboard'])
          }
        else {
            console.log(`in else ${response['response_message']}`)
            this.service.toastErr(response['response_message'])
        }
      }, error=>{
      console.log('error occur',error)
      this.service.toastErr(`Something went wrong`)
    })  
  }
}
